import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"
import { getBalanceSheetFiscalPeriod } from "../../utils/fiscalPeriodUtils"

function debtToMarketCapTtm(tableName: string) {
  const title = "Debt To Market Cap (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["debtToMarketCapTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Debt as a share of market value.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            const fiscalPeriod = getBalanceSheetFiscalPeriod(info.row.original)
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} reported in ${fiscalPeriod} about debt to market cap or other relevant financial measures?`,
              value: info.row.original.financialData.companyRatios?.data?.debtToMarketCapTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            (info.row.original.financialData.companyRatios?.data?.debtToMarketCapTtm ?? 0) * 100
          }
        />
      )
    },
  })
}

export { debtToMarketCapTtm }
